import React from 'react';
import MessagingTemplate from './components/MessagingTemplate';
import {
  searchUrl,
  name,
  maxSelectionText,
  placeholderText,
  mockServiceCalls,
  sendMessageResponseShape,
  replyResponseShape,
} from './common';

export default function Example() {
  React.useEffect(() => {
    mockServiceCalls();
  }, []);

  return (
    <MessagingTemplate
      title="Multiple Searchable Select Example"
      note={(
        <React.Fragment>
          <p>
          For this workflow, the shape of the response is expected to be the&nbsp;
            <strong>Send Message workflow response shape</strong>
            , as opposed to the reply/reply all response shape
          </p>
          <p>
            Send message response shape:&nbsp;
            <strong>{sendMessageResponseShape}</strong>
          </p>
          <p>
            Reply/Reply All response shape:&nbsp;
            <strong>{replyResponseShape}</strong>
          </p>
        </React.Fragment>
      )}
    >
      <input
        id="id_recipient"
        type="hidden"
        name={name}
        data-dynamic-list
        data-format-input={maxSelectionText}
        data-placeholder-text={placeholderText}
        data-search-url={searchUrl}
        data-maximum-selection="10"
        data-multiple="True"
      />
    </MessagingTemplate>
  );
}
