import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames/bind';
import StaticSelect from './StaticSelect/StaticSelect';
import styles from './Select.module.scss';
import times from '../utils/times';

const cx = classNames.bind(styles);

const propTypes = {
  selectId: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  options: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string.isRequired,
    text: PropTypes.string.isRequired,
  })),
  isMultiple: PropTypes.bool,
  placeholder: PropTypes.string,
  maxSelection: PropTypes.number,
  maxSelectionText: PropTypes.node,
  initialSelection: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string.isRequired,
    text: PropTypes.string.isRequired,
  })),
};

const defaultProps = {
  options: [],
  isMultiple: false,
  placeholder: undefined,
  maxSelection: undefined,
  maxSelectionText: undefined,
  initialSelection: [],
};

function initialSelectedMap(initialSelections = []) {
  return initialSelections.reduce((acc, { id }) => ({
    ...acc,
    [id]: true,
  }), {});
}

function StaticSelection({
  selectId,
  name,
  isMultiple,
  options,
  placeholder,
  maxSelection,
  maxSelectionText,
  initialSelection,
}) {
  const hiddenSelect = React.useRef(null);
  const [selected, setSelected] = React.useState(initialSelection);
  const [selectedMap, setSelectedMap] = React.useState(initialSelectedMap(initialSelection));

  const isOptionSelected = React.useCallback((id, map = selectedMap) => (
    map[id] !== undefined && map[id]
  ), [selectedMap]);

  const onChange = React.useCallback((selectedIds) => {
    const newSelectedMap = selectedIds.reduce((acc, id) => ({
      ...acc,
      [id]: true,
    }), []);
    const selections = options.filter(option => isOptionSelected(option.id, newSelectedMap));
    setSelectedMap(newSelectedMap);
    setSelected(selections);
  }, [isOptionSelected, options]);

  const selectProps = {
    title: placeholder,
    options,
    isMultiple,
    allowClear: true,
    placeholder,
    maxSelection,
    maxSelectionText,
    selected,
  };

  React.useEffect(() => {
    if (!hiddenSelect.current) {
      return;
    }

    times(hiddenSelect.current.options.length, (i) => {
      const option = hiddenSelect.current.options.item(i);
      option.selected = isOptionSelected(option.value);
    });
  }, [isOptionSelected, selected, selectedMap]);

  return (
    <div>
      <select
        ref={hiddenSelect}
        className={cx(['hidden-select'])}
        id={selectId}
        name={name}
        multiple={isMultiple}
      >
        {options.map(({ id, text }) => (
          <option key={id} value={id}>
            {text}
          </option>
        ))}
      </select>
      <StaticSelect {...selectProps} onChange={onChange} />
    </div>
  );
}

StaticSelection.propTypes = propTypes;
StaticSelection.defaultProps = defaultProps;

export default StaticSelection;
