import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames/bind';
import uniqueId from 'lodash.uniqueid';
import VisuallyHiddenText from 'terra-visually-hidden-text';
import MultiSelect from '../../../terra-form-select/src/MultiSelect';
import SingleSelect from '../../../terra-form-select/src/SingleSelect';
import contains from '../../utils/contains';

import styles from '../Select.module.scss';

const cx = classNames.bind(styles);

const propTypes = {
  options: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string.isRequired,
    text: PropTypes.string.isRequired,
  })),
  isMultiple: PropTypes.bool,
  placeholder: PropTypes.string,
  maxSelection: PropTypes.number,
  maxSelectionText: PropTypes.node,
  onChange: PropTypes.func,
  selected: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string.isREquired,
    text: PropTypes.string.isRequired,
  })).isRequired,
};

const defaultProps = {
  options: [],
  isMultiple: false,
  placeholder: undefined,
  maxSelection: undefined,
  maxSelectionText: undefined,
  onChange: () => {},
};

function StaticSelect({
  options,
  isMultiple,
  placeholder,
  maxSelection,
  maxSelectionText,
  onChange,
  selected,
}) {
  const selectedValues = React.useMemo(
    () => [].concat(selected).map(({ id }) => id),
    [selected],
  );

  const maxAllowedSelections = isMultiple ? maxSelection : undefined;
  const atMaxSelection = maxAllowedSelections
    ? selectedValues.length >= maxAllowedSelections
    : false;

  const noResultContent = React.useCallback(() => {
    if (atMaxSelection) {
      return maxSelectionText;
    }

    return undefined;
  }, [atMaxSelection, maxSelectionText]);

  const optionFilter = React.useCallback((searchValue, option) => (
    contains(option.props.display, searchValue)
    && !atMaxSelection
  ), [atMaxSelection]);

  const handleOnChange = React.useCallback((value) => {
    const selection = [].concat(value);
    onChange(selection);
  }, [onChange]);

  const { current: placeholderId } = React.useRef(uniqueId('recipient-help-text-'));
  const Select = isMultiple ? MultiSelect : SingleSelect;

  const multipleProps = {
    inputDisabled: atMaxSelection,
    isTouchAccessible: true,
    optionFilter,
    noResultAriaText: noResultContent(),
    noResultContent: noResultContent(),
    value: selectedValues,
  };

  const singleProps = {
    value: selectedValues[0],
  };

  return (
    <div>
      <VisuallyHiddenText
        aria-live="assertive"
        aria-relevant="additions text"
        text={atMaxSelection ? maxSelectionText : ''}
      />
      <Select
        className={cx(['select'])}
        aria-describedby={placeholderId}
        placeholder=""
        onChange={handleOnChange}
        closeDropdownOnChange
        {...(isMultiple ? multipleProps : singleProps)}
      >
        {options.map(({ id, text }) => (
          <Select.Option key={id} value={id} display={text} />
        ))}
      </Select>
      <span tabIndex="-1" id={placeholderId} className={cx(['placeholder'])}>
        {placeholder}
      </span>
    </div>
  );
}

StaticSelect.propTypes = propTypes;
StaticSelect.defaultProps = defaultProps;

export default StaticSelect;
