import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { FormattedMessage, injectIntl, intlShape } from 'react-intl';
import classNames from 'classnames/bind';
import uniqueId from 'lodash.uniqueid';
import MultiSelect from '../../../terra-form-select/src/MultiSelect';

import styles from '../Select.module.scss';
import useAjax from '../../hooks/useAjax';
import useDebounce from '../../hooks/useDebounce';
import contains from '../../utils/contains';
import buildUrl from '../../utils/buildUrl';
import difference from '../../utils/difference';

const cx = classNames.bind(styles);

const propTypes = {
  searchUrl: PropTypes.string.isRequired,
  placeholder: PropTypes.string,
  maxSelection: PropTypes.number,
  maxSelectionText: PropTypes.node,
  searchThreshold: PropTypes.number,
  extraSearchQueryParams: PropTypes.string,
  onChange: PropTypes.func,
  selected: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string.isREquired,
    text: PropTypes.string.isRequired,
  })).isRequired,
  isMultiple: PropTypes.bool,
  intl: intlShape.isRequired,
};

const defaultProps = {
  placeholder: undefined,
  maxSelection: undefined,
  maxSelectionText: undefined,
  searchThreshold: undefined,
  extraSearchQueryParams: '',
  onChange: () => {},
  isMultiple: false,
};

const SearchableSelect = ({
  searchUrl,
  placeholder,
  maxSelection,
  maxSelectionText,
  searchThreshold,
  extraSearchQueryParams,
  onChange,
  selected,
  isMultiple,
  intl,
}) => {
  const { data, isLoading, doFetch } = useAjax('', []);
  const [query, setQuery] = React.useState('');
  const debouncedQuery = useDebounce(query, 250);

  const selectedValues = React.useMemo(
    () => [].concat(selected).map(({ id }) => id),
    [selected],
  );

  const displayContent = (searchThreshold
    ? query.length >= searchThreshold
    : true
  );

  const maxAllowedSelections = isMultiple ? maxSelection : 1;
  const atMaxSelection = maxAllowedSelections
    ? selectedValues.length >= maxAllowedSelections
    : false;

  React.useEffect(() => {
    if (atMaxSelection) {
      return;
    }

    if (debouncedQuery) {
      const url = buildUrl(searchUrl, debouncedQuery, extraSearchQueryParams);

      if (!searchThreshold) {
        doFetch(url);
      } else if (debouncedQuery.length >= searchThreshold) {
        doFetch(url);
      }
    }
  }, [atMaxSelection, debouncedQuery, doFetch, extraSearchQueryParams, searchThreshold, searchUrl]);

  // memoize our content, since it can be expensive to calc
  const content = React.useMemo(() => {
    const nonSelected = difference(data, selected);
    const merged = [...selected, ...nonSelected].reduceRight(
      (acc, { id, text }) => ({ ...acc, [id]: text }),
      [],
    );

    return Object.entries(merged)
      .map(([id, text]) => ({ text, id }))
      .sort((lhs, rhs) => (lhs.text <= rhs.text ? -1 : 1));
  }, [data, selected]);

  const onSearch = React.useCallback((value) => {
    setQuery(value);
  }, []);

  const handleOnSelect = React.useCallback((value) => {
    const newSelection = content.find(selection => selection.id === value);
    const newSelectedData = (isMultiple
      ? [...selected, newSelection]
      : newSelection
    );

    onChange(newSelectedData);
    setQuery('');
  }, [content, isMultiple, onChange, selected]);

  const handleOnDeselect = React.useCallback((value) => {
    const newSelectedData = selected.filter(selection => selection.id !== value);
    onChange(newSelectedData);
    setQuery('');
  }, [onChange, selected]);

  const optionFilter = React.useCallback((search, option) => (
    !atMaxSelection
    && !isLoading
    && displayContent
    && contains(option.props.display, search)
  ), [atMaxSelection, displayContent, isLoading]);

  const displayNoResultsContent = React.useCallback(() => {
    if (atMaxSelection) {
      return maxSelectionText;
    }

    if (!displayContent) {
      const count = searchThreshold - query.length;
      return (
        <FormattedMessage
          id="PortalTerra.Select.enterMoreCharacters"
          values={{ count }}
        />
      );
    }

    if (isLoading || (query && !debouncedQuery)) {
      return <FormattedMessage id="PortalTerra.Select.searching" />;
    }

    return undefined;
  }, [atMaxSelection,
    debouncedQuery,
    displayContent,
    isLoading,
    maxSelectionText,
    query,
    searchThreshold,
  ]);

  const { current: placeholderId } = React.useRef(uniqueId('recipient-help-text-'));

  const noResultsAriaText = React.useCallback(() => {
    if (displayContent) {
      return undefined;
    }

    if (atMaxSelection) {
      return maxSelectionText;
    }

    const count = searchThreshold - query.length;
    return intl.formatMessage({ id: 'PortalTerra.Select.enterMoreCharacters' }, { count });
  }, [atMaxSelection, displayContent, intl, maxSelectionText, query.length, searchThreshold]);

  return (
    <Fragment>
      <MultiSelect
        className={cx(['select'])}
        aria-describedby={placeholderId}
        isTouchAccessible
        placeholder=""
        onSearch={onSearch}
        onSelect={handleOnSelect}
        onDeselect={handleOnDeselect}
        optionFilter={optionFilter}
        value={selectedValues}
        noResultContent={displayNoResultsContent()}

        // new, non-standard props follow
        closeDropdownOnChange
        noResultAriaText={noResultsAriaText()}
        isLoading={isLoading}
        hideNoContent={!isMultiple && atMaxSelection}
        isAsync
        inputDisabled={atMaxSelection}
        // TODO: uncomment these out next
        // focusAfterSelection={!isMultiple}
      >
        {content.map(({ id, text }) => (
          <MultiSelect.Option key={id} value={id} display={text} />
        ))}
      </MultiSelect>
      <span tabIndex="-1" id={placeholderId} className={cx(['placeholder'])}>
        {placeholder}
      </span>
    </Fragment>
  );
};

SearchableSelect.propTypes = propTypes;
SearchableSelect.defaultProps = defaultProps;

export default injectIntl(SearchableSelect);
