import React from 'react';
import ajax from '../utils/ajax';

export function ajaxReducer(state, { type, payload } = {}) {
  switch (type) {
    case 'FETCH_INIT':
      return {
        ...state,
        isLoading: true,
        isError: false,
      };
    case 'FETCH_SUCCESS':
      return {
        ...state,
        isLoading: false,
        isError: false,
        data: payload,
      };
    case 'FETCH_FAILURE':
      return {
        ...state,
        isLoading: false,
        isError: true,
      };
    case 'FETCH_NO_URL':
      return {
        ...state,
        isLoading: false,
        isError: false,
      };
    case 'RESET_DATA':
      return {
        ...state,
        data: [],
      };
    default:
      throw new Error();
  }
}

export default function useAjax(initialUrl, initialData) {
  const [url, setUrl] = React.useState(initialUrl);

  const [state, dispatch] = React.useReducer(ajaxReducer, {
    isLoading: false,
    isError: false,
    data: initialData,
  });

  React.useEffect(() => {
    let didCancel = false;

    const fetchData = async () => {
      if (!url) {
        dispatch({ type: 'FETCH_NO_URL' });
        return;
      }

      dispatch({ type: 'FETCH_INIT' });

      try {
        /** Story time:
         *
         * So, the expected response shape from portal is:
         * [{ id: String, text: String }, ...]
         * However, this isn't the case for the Reply/Reply All workflow 🙃,
         * hence this logic.
         * The reply/reply all workflow response shape is:
         * { items: [{ id: String, text: String }, ...] }
         *
         * Leave it be or else.
         */
        const response = await ajax(url);
        const payload = response.items ? response.items : response;

        if (!didCancel) {
          dispatch({ type: 'FETCH_SUCCESS', payload });
        }
      } catch (error) {
        if (!didCancel) {
          dispatch({ type: 'FETCH_FAILURE' });
        }
      }
    };

    fetchData();

    return () => {
      didCancel = true;
    };
  }, [url]);

  const doFetch = React.useCallback((fetchUrl) => {
    setUrl(fetchUrl);
  }, []);

  const resetData = React.useCallback(() => {
    dispatch({ type: 'RESET_DATA' });
  }, []);

  return { ...state, doFetch, resetData };
}
