import fetchMock from 'fetch-mock'; // eslint-disable-line import/no-extraneous-dependencies
import names from './mockData/names';
import contains from '../../utils/contains';

export const name = 'recipient';
export const placeholderText = 'Select a recipient';
export const maxSelectionText = 'Maximum amount of recipients has been reached. You can start a new message to include additional recipients.';
export const searchUrl = '/routing/general-message';
export const displayNone = {
  display: 'none',
};

function randomDelay(min = 100, max = 3000) {
  return new Promise(resolve => setTimeout(resolve, Math.random() * (max - min) + min));
}

export function mockServiceCalls(options = {}) {
  const { isWeirdReplyWorkflow } = options;

  async function serverResponse(url) {
    const params = new URL(url, 'http://localhost:8080/').searchParams;
    const q = params.get('q');
    /* eslint-disable no-console */
    console.group('fetching');
    console.warn({ url });
    const items = await randomDelay(250, 3000).then(() => (
      names.filter(({ text }) => contains(text, q)).filter((_, i) => i < 50)
    ));

    const result = isWeirdReplyWorkflow ? { items } : items;
    console.warn({ url, result });
    console.groupEnd();
    /* eslint-enable no-console */

    return result;
  }

  fetchMock.restore().mock('*', serverResponse);
}

export const sendMessageResponseShape = '[{ id: String, text: String }, ...]';
export const replyResponseShape = '{ items: [{ id: String, text: String }, ...] }';
