import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames/bind';
import styles from './MessagingTemplate.module.scss';
import Card from './Card';
import Required from './Required';
import { dynamicList, dropdownList } from '../../..';

const cx = classNames.bind(styles);

export default function MessagingTemplate({ children, title, note }) {
  const [postBody, setPostBody] = React.useState([]);

  React.useEffect(() => {
    document
      .querySelectorAll('[data-dynamic-list]')
      .forEach(e => dynamicList(e, { containerClass: 'foo' }));
    document
      .querySelectorAll('[data-dropdown-list]')
      .forEach(e => dropdownList(e));
  });

  function onSubmit(event) {
    event.preventDefault();

    const data = [...new FormData(event.target)]
      .filter(([k]) => k !== 'removeme')
      .reduce((acc, [key, value]) => [...acc, { key, value }], []);

    setPostBody(data);
  }

  return (
    <div className={cx(['content'])}>
      <Card>
        <h1>{title}</h1>
      </Card>
      {note && <Card>{note}</Card>}
      <Card>
        <form id="sendMessageForm" encType="multipart/form-data" action="/messaging/compose" method="POST" onSubmit={onSubmit}>
          <div className={cx(['field-required-message'])}>
            <Required />
Indicates a required field.
          </div>
          <div className={cx(['section'])}>
            <label className={cx(['sent-on-behalf'])} htmlFor="person">This request is sent on behalf of</label>
            <input name="removeme" readOnly className={cx(['messaging-input', 'read-only-input'])} value="Mickey Baker" />
            <input id="id_person" type="hidden" name="person" value="some_person_id_abc123" />
          </div>
          <div className={cx(['section'])}>
            <label htmlFor="id_recipient" className={cx(['boldylocks'])}>
              <Required />
To
            </label>
            {children}
          </div>
          <div className={cx(['section'])}>
            <label htmlFor="id_subject" className={cx(['boldylocks'])}>
              <Required />
            Subject
            </label>
            <input id="id_subject" name="subject" type="text" className={cx(['messaging-input'])} />
          </div>
          <div className={cx(['section'])}>
            <label htmlFor="attachments" className={cx(['boldylocks'])}>Attachments</label>
            <input className={cx(['messaging-input', 'fake-attachments'])} readOnly name="attachments" value="Attachments go here.." />
          </div>
          <div className={cx(['section'])}>
            <label htmlFor="id_message" className={cx(['boldylocks'])}>
              <Required />
            Message
            </label>
            <textarea id="id_message" name="message" rows="10" maxLength="600" className={cx(['messaging-input'])} />
            <small tabIndex="-1" id="id_message-help" className={cx(['field-help'])}>XXX characters remaining (600 maximum).</small>
          </div>
          <button type="submit" className={cx(['send-button'])}>Send</button>
          <a onClick={() => setPostBody([])} className={cx(['cancel-button'])}>Cancel</a> {/* eslint-disable-line */}
        </form>
        <div className={cx('post-body')}>
          <h2>Post Body:</h2>
          <ul className={cx('post-body-list')}>
            {postBody.map(({ key, value }) => (
              <li key={key + value} className={cx('post-body-list-item')}>
                {key}
              :&nbsp;
                {value}
              </li>
            ))}
          </ul>
        </div>
      </Card>
    </div>
  );
}

MessagingTemplate.propTypes = {
  children: PropTypes.node.isRequired,
  title: PropTypes.string.isRequired,
  note: PropTypes.node,
};
