import parseBool from './parseBool';

export default function extractDataset(node = {}) {
  const dataset = { ...((node || {}).dataset || {}) };
  const {
    initvalue = '[]',
    placeholderText,
    maximumSelection,
    formatInput,
    multiple,
    searchUrl,
    additionalQueryParams,
  } = dataset;

  const placeholder = placeholderText || undefined;
  const maxSelection = parseInt(maximumSelection, 10) || 1;
  const isMultiple = (node.multiple !== undefined && parseBool(node.multiple))
    || (multiple !== undefined && parseBool(multiple));
  const initialSelection = [].concat(JSON.parse(initvalue)).filter(Boolean);

  return {
    placeholder,
    isMultiple,
    maxSelection,
    maxSelectionText: formatInput,
    initialSelection,
    searchUrl,
    searchThreshold: 2,
    extraSearchQueryParams: additionalQueryParams,
  };
}
