import removeTrailingSlashes from './removeTrailingSlashes';

export default function buildUrl(baseUrl, query = '', optionalParams = '') {
  const encodedQuery = encodeURIComponent(query);
  const otherQuery = optionalParams ? `&${optionalParams}` : '';
  const queryString = encodedQuery || otherQuery ? `?q=${encodedQuery}${otherQuery}` : '';
  return `${removeTrailingSlashes(baseUrl)}${queryString}`;
}
