import React from 'react';
import PropTypes from 'prop-types';
import SearchableSelect from './SearchableSelect/SearchableSelect';

const propTypes = {
  selectId: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  searchUrl: PropTypes.string.isRequired,
  searchThreshold: PropTypes.number,
  extraSearchQueryParams: PropTypes.string,
  isMultiple: PropTypes.bool,
  placeholder: PropTypes.string,
  maxSelection: PropTypes.number,
  maxSelectionText: PropTypes.node,
  initialSelection: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string.isRequired,
    text: PropTypes.string.isRequired,
  })),
};

const defaultProps = {
  isMultiple: false,
  placeholder: undefined,
  maxSelection: undefined,
  maxSelectionText: undefined,
  initialSelection: [],
};

function SearchableSelection({
  selectId,
  name,
  searchUrl,
  searchThreshold,
  extraSearchQueryParams,
  isMultiple,
  placeholder,
  maxSelection,
  maxSelectionText,
  initialSelection,
}) {
  const [selected, setSelected] = React.useState(initialSelection);
  const hiddenData = React.useMemo(() => selected.map(({ id }) => id), [selected]);

  const handleOnChange = React.useCallback((value) => {
    setSelected([].concat(value));
  }, []);

  const selectProps = {
    title: placeholder,
    isMultiple,
    allowClear: true,
    placeholder,
    maxSelection,
    maxSelectionText,
    searchUrl,
    searchThreshold,
    extraSearchQueryParams,
    selected,
  };

  return (
    <div>
      <input type="hidden" id={selectId} name={name} value={hiddenData} />
      <SearchableSelect {...selectProps} onChange={handleOnChange} />
    </div>
  );
}

SearchableSelection.propTypes = propTypes;
SearchableSelection.defaultProps = defaultProps;

export default SearchableSelection;
