import React from 'react';
import { render } from 'react-dom';
import classNames from 'classnames/bind';
import extractDataset from './utils/extractDataset';
import getLocale from './utils/getLocale';
import getContainer from './utils/getContainer';
import styles from './Container.module.scss';
import SelectBase from './components/SelectBase/SelectBase';
import SearchableSelection from './components/SearchableSelection';

const cx = classNames.bind(styles);

export default function dynamicList(input, opts) {
  const { containerClass = '', assetPath = '' } = opts || {};
  const { name, id } = input;

  if (assetPath) {
    __webpack_require__.p = assetPath; // eslint-disable-line no-undef
  }

  const locale = getLocale();
  const dataset = extractDataset(input);
  const container = getContainer(input, { replace: true });

  container.className = `${cx('terra-select-container')} ${containerClass || ''}`;

  const props = {
    selectId: id,
    name,
    ...dataset,
  };

  const component = (
    <SelectBase locale={locale}>
      <SearchableSelection {...props} />
    </SelectBase>
  );

  render(component, container);
}
